<template>
    <div>
        <!-- <select class="form-select" 
                id="select-geo"
                autocomplete="off"
                v-model = "selected"
                placeholder="Sélectionnez un territoire"
                :onchange="onChange">
            <option v-for="cv in cvList" :value="cv.codgeo" :key="cv.codgeo"> 
                {{ cv.libgeo}} ({{ cv.codgeo }})
            </option>
        </select> -->
        <label for="select-territoire">Territoire</label>
        <v-select :options="cvList"
                :get-option-label="e => `${e.libgeo} (${e.codgeo})`" 
                :reduce="e => e.codgeo"
                placeholder="Nom, code ..."
                v-model="selected"
                id="select-territoire"
                class="select-input"/>
    </div>
</template>

<script>
import vSelect from 'vue-select'
import 'vue-select/dist/vue-select.css';
// https://codepen.io/anon/pen/PgyLXz

export default {
    name:'List',
    props:['filterCodeFromStore'],
    components:{
        'v-select':vSelect,
    },
    data() {
        return {
            selected:null
        }
    },
    computed:{
        cvList() {
            return this.$store.state.cvList
        }
    },
    updated() {
         if(this.selected) {
             this.onChange()
         } else {
            this.$store.dispatch('resetCodegeo')
         }
    },
    watch: {
        filterCodeFromStore() {
            this.selected = this.filterCodeFromStore
        }
    },
    methods: {
        onChange() {
            this.$store.state.filterCode = this.selected;
            this.$store.commit('crossFilter', {
                type:'cdv',
                value:this.selected
            })
        }
    }
}
</script>

<style scoped>
    /* select {
        display: block;
    } */

    .select-input {
        background: white;
        z-index: 10000 !important;
    }
</style>